.card {
  // FUNCTIONALITY

  perspective: 150rem;
  -moz-perspective: 150rem;
  position: relative;
  height: 52rem;

  &__side {
    height: 52rem;
    transition: all 0.8s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 3px;
    overflow: hidden;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.15);

    &--front {
      background-color: $color-white;
    }

    &--back {
      transform: rotateY(180deg);

      &--1 {
        @include card-background($color-secondary-light, $color-secondary-dark);
      }

      &--2 {
        @include card-background($color-primary-light, $color-primary-dark);
      }

      &--3 {
        @include card-background($color-tertiary-light, $color-tertiary-dark);
      }
    }
  }

  &:hover &__side--front {
    transform: rotateY(-180deg);
  }

  &:hover &__side--back {
    transform: rotateY(0);
  }

  // FRONT SIDE STYLING
  &__picture {
    background-size: cover;
    height: 23rem;
    background-blend-mode: screen;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);

    &--1 {
      @include card-background(
        $color-secondary-light,
        $color-secondary-dark,
        1,
        url(../images/nat-5.jpg)
      );
    }

    &--2 {
      @include card-background(
        $color-primary-light,
        $color-primary-dark,
        1,
        url(../images/nat-6.jpg)
      );
    }

    &--3 {
      @include card-background(
        $color-tertiary-light,
        $color-tertiary-dark,
        1,
        url(../images/nat-7.jpg)
      );
    }
  }

  &__heading {
    font-size: 2.8rem;
    font-weight: 300;
    text-transform: uppercase;
    text-align: right;
    color: $color-white;
    position: absolute;
    top: 12rem;
    right: 2rem;
    width: 75%;
  }

  &__heading-span {
    padding: 1rem 1.5rem;
    -webkit-box-decoration-break: clone;
    box-decoration-break: clone;

    &--1 {
      @include card-background(
        $color-secondary-light,
        $color-secondary-dark,
        0.85
      );
    }

    &--2 {
      @include card-background($color-primary-light, $color-primary-dark, 0.85);
    }

    &--3 {
      @include card-background(
        $color-tertiary-light,
        $color-tertiary-dark,
        0.85
      );
    }
  }

  &__details {
    padding: 3rem;

    ul {
      list-style: none;
      width: 80%;
      margin: 0 auto;

      li {
        text-align: center;
        font-size: 1.5rem;
        padding: 1rem;

        &:not(:last-child) {
          border-bottom: 1px solid $color-grey-light-2;
        }
      }
    }
  }

  // BACK SIDE STYLING

  &__cta {
    @include center-hor-ver;
    width: 90%;
    text-align: center;
  }

  &__price-box {
    color: $color-white;
    margin-bottom: 8rem;
    text-align: center;
  }

  &__price-only {
    font-size: 1.4rem;
    text-transform: uppercase;
  }

  &__price-value {
    font-size: 6rem;
    font-weight: 100;
  }

  @media only screen and (max-width: 56.25em), only screen and (hover: none) {
    // FUNCTIONALITY
    height: auto;
    border-radius: 3px;
    background-color: $color-white;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.15);

    &__side {
      height: auto;
      position: relative;
      box-shadow: none;

      &--back {
        transform: rotateY(0);
        -webkit-clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
        clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
        border-top-right-radius: 0;
      }
    }

    &:hover &__side--front {
      transform: rotateY(0);
    }
    // BACK SIDE STYLING

    &__cta {
      position: relative;
      top: 0;
      left: 1.5rem;
      transform: translate(0);
      width: 90%;
      padding: 7rem 4rem 4rem 4rem;
    }

    &__details {
      padding: 1rem 3rem;
    }

    &__price-box {
      margin-bottom: 3rem;
    }

    &__price-value {
      font-size: 6rem;
    }
  }
}
